import { __ } from '@wordpress/i18n';
import { useRef, useEffect } from '@wordpress/element';

const Notice = ({ notice, message = '' }) => {
    const noticeRef = useRef(null);

    useEffect(() => {
        // remove show class after 5 seconds
        if (notice) {
            setTimeout(() => {
                noticeRef.current.classList?.remove('show');
            }, 1000);
        }
    }, [notice]);

    return (
        <div className={`data-update-notice ${notice ? 'show' : ''}`} ref={noticeRef}>
            <svg width="16" height="16" fill="currentColor" viewBox="0 0 16 16">
                <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14m0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16" />
                <path d="m10.97 4.97-.02.022-3.473 4.425-2.093-2.094a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-1.071-1.05" />
            </svg>
            {message ? message : __('Data updated successfully.', 'zoloblocks')}
        </div>
    );
};

export default Notice;
